import { DASHBOARD_DATA, DASHBOARD_POPULAR_PRODUCTS, DASHBOARD_PENDING_ORDERS } from "../types"

const initialstate = {
    popularProducts: [],
    dashboardData: [],
    pendingOrders: []
}

export default function dashboard_reducers(state = initialstate, action) {
    switch (action.type) {
        case DASHBOARD_POPULAR_PRODUCTS:
            return {
                ...state,
                popularProducts: action.data
            }
        case DASHBOARD_DATA:
            return {
                ...state,
                dashboardData: action.data
            }
        case DASHBOARD_PENDING_ORDERS:
            return {
                ...state,
                pendingOrders: action.data
            }
        default:
            return state;
    }
}